<template>
  <div>
    <div class="collectionNav lightest-bg"></div>
    <div class="collection">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="经典" name="classic">
          <div class="classics-text" v-if="collectionClassic !==''">
            <div class="bookCtion" v-for="(item,index) in collectionClassic" :key="index">
              <div class="bookImg">
                <router-link :to="{path:'/classic/details'}" class="lightest-box">
                  <img
                    :src="item.images"
                    :alt="item.title"
                    @click="goNews(item,i,book)"
                    v-if="item.images !==undefined"
                  >
                  <img src="../../../assets/img/back.png" :alt="item.title" v-else>
                </router-link>
              </div>
              <p class="classic-title">{{item.title}}</p>
              <a href="javascript:void(0)" @click="goNews(item,index)" class="lightest-box">
                <div class="bookBt" @click="goNews(item)">立刻收听</div>
              </a>
            </div>
          </div>
          <div class="text-else" v-else>
            <p>很抱歉，未能找到相关搜索结果！</p>
          </div>
        </el-tab-pane>
        <el-tab-pane label="视频" name="video">
          <div class="video-list" v-if="showList !==''">
            <div class="myVideo" v-for="(item,i) in showList" :key="i" id="myVideo">
              <img :src="item.images" class="title" v-if="item.images!==undefined">
              <img src="../../../assets/img/back.png" class="title" v-else>
              <a href="javascript:void(0)" @click="goVideo(item,i)">
                <span class="iconfont icon-bofang lightest-text" id="play-picture"></span>
              </a>
              <p class="text-1">{{item.title}}</p>
            </div>
          </div>
          <div class="text-else" v-else>
            <p>很抱歉，未能找到相关搜索结果！</p>
          </div>
        </el-tab-pane>
        <el-tab-pane label="问答" name="question">
          <div v-if="showList !==''">
            <div class="question-text lightest-bg" v-for="(item,i) in showList" :key="i">
              <div class="text-top">
                <img :src="item.avatar " alt v-if="item.avatar !==null">
                <!-- <span class="iconfont icon-lihansongjing questionAvatar" v-else></span> -->
                <img src="https://mhb.hanyujingyun.cn/images/lihansongjing.svg" v-else>
                <span class="name light-text">{{item.nickname?item.nickname:'匿名'}}</span>
                <!-- <span class="name_time">{{item.timecreated}}</span> -->
                <div class="show">
                  <span class="Qs darkest-text">{{item.content}}</span>
                </div>
              </div>
              <div class="center" v-if="item.answerAudio==''">
                <div v-html="item.answerContent" class="center-text medium-text"></div>
              </div>
              <div class="center" v-else>
                <div class="answerAudio theme-bg">
                  <div class="answerAudio-box">
                    <div class="answerAudio-btn" @click="play(item,i)">
                      <span class="iconfont icon-playingIcon" v-show="flag==false"></span>
                      <span class="iconfont icon-playIconsm" v-show="flag==true"></span>
                    </div>
                    <span class="timer">{{timer}}秒</span>
                  </div>
                </div>
              </div>
              <!-- <div class="qrTime">
                <span class="time light-text">{{item.timecreated}}</span>
              </div>-->
              <div class="text-bottom">
                <ul>
                  <li>
                    <a href="javascript:void(0)" @click="share(item,i)">
                      <span class="iconfont icon-fenxiang light-text"></span>
                    </a>
                  </li>
                  <li>
                    <a href="javascript:void(0)" @click="collect(item,i)">
                      <span
                        class="iconfont icon-shoucang light-text"
                        :class="{iconfont:!isShow,show:isShow}"
                      ></span>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div class="text-else" v-else>
            <p>很抱歉，未能找到相关搜索结果！</p>
          </div>
        </el-tab-pane>
        <!-- <el-tab-pane label="活动" name="activity">
          <div class="classics-text" v-if="showList !==undefined">
            <div class="book" v-for="(item,index) in showList" :key="index">
              <img :src="item.images" alt>
              <a href="javascript:void(0)" @click="goNews(item,index)" class="lightest-box">
                <div class="bookBt" @click="goNews(item)">立刻收听</div>
              </a>
            </div>
          </div>
          <div class="text-else" v-else>
            <p>很抱歉，未能找到相关搜索结果！</p>
          </div>
        </el-tab-pane>-->
        <!-- <el-tab-pane label="产品" name="product">
          <div class="classics-text" v-if="showList !==undefined">
            <div class="book" v-for="(item,index) in showList" :key="index">
              <img :src="item.images.detail" alt>
              <a href="javascript:void(0)" @click="goNews(item,index)" class="lightest-box">
                <div class="bookBt" @click="goNews(item)">立刻收听</div>
              </a>
            </div>
          </div>
          <div class="text-else" v-else>
            <p>很抱歉，未能找到相关搜索结果！</p>
          </div>
        </el-tab-pane>-->
        <!-- <el-tab-pane label="课程" name="vlog">
          <div class="classics-text" v-if="showList !==undefined">
            <div class="book" v-for="(item,index) in showList" :key="index">
              <img :src="item.images" alt>
              <a href="javascript:void(0)" @click="goNews(item,index)" class="lightest-box">
                <div class="bookBt" @click="goNews(item)">立刻收听</div>
              </a>
            </div>
          </div>
          <div class="text-else" v-else>
            <p>很抱歉，未能找到相关搜索结果！</p>
          </div>
        </el-tab-pane>-->
      </el-tabs>
    </div>
    <el-dialog
      :title="qrTitle"
      :visible.sync="shareShow"
      id="scanDialoge"
      :close-on-click-modal="false"
      show-close
    >
      <QrcodeVue :value="qrText" size="200" class="qrCode"/>
      <!-- <img id="qrCode" :src="imgUrl" alt> -->
      <p>{{tipInfo}}</p>
    </el-dialog>
  </div>
</template>
<script>
import QrcodeVue from "qrcode.vue";
export default {
  data() {
    return {
      flag: true,
      activeName: "classic",
      type: "",
      isShow: false,
      qrTitle: "",
      tipInfo: "",
      qrText: "",
      shareShow: false,
      showList: ""
    };
  },
  mounted() {},
  methods: {
    goNews(item, index) {
      var _this = this;
      localStorage.setItem("ClassicId", item.id);
      this.$router.push({
        path: "/classic/details", //跳转的路由
        name: "classicDetails" //路由的name
      });
    },
    goVideo(item, i) {
      var _this = this;
      this.$router.push({
        path: "/video/details"
      });
      this.$store.commit("setVideo", item);
    },
    handleClick(tab, event) {
      // if(tab.name=='classic'){}
      this.type = tab.name;
      this.collectionList();
    },
    // 收藏列表
    collectionList(type) {
      var _this = this;
      var url = this.APIURL + "/classic/usr/collect/all";
      var data = {
        pageSize: 1000,
        type: this.type
      };
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(res => {
          // console.log(res.data);
          _this.showList = res.data.data.dataSet;
          // console.log(res.data);
        })
        .catch(err => {});
    },
    // 分享
    share(item, i) {
      this.shareShow = true;
      this.qrText = "https://m.lihan.ltd/#/pages/QA/QAdetail?curId=" + item.id;
      // "&app=false&bgclr=false";
      this.qrTitle = "微信扫描二维码进行分享";
    },
    collect(item, i) {
      var _this = this;
      let data = {
        type: "question",
        target: item.id,
        operation: "collect"
      };
      var url = this.APIURL + "/classic/question/behavior";
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(function(data) {
          if (data.data.errorCode == 0) {
            // console.log(data);
            _this.$message({
              message: "收藏成功",
              type: "success"
            });
          } else {
            alert(data.data.msg);
          }
        });
    }
  },
  watch: {
    SessionKey(val) {
      this.collectionList();
    },
    timer() {}
  },
  computed: {
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    },
    collectionClassic() {
      return this.$store.state.collectionClassic;
    },
    timer(min, max) {
      return Math.round(Math.random() * 10 + 10);
    }
  },
  components: {
    QrcodeVue
  }
};
</script>
<style lang="scss">
.collectionNav {
  width: 100%;
  margin: 0 auto;
  height: 90px;
}
.collection {
  width: 100%;
  margin: 10px auto 0;
  background-color: #fff;
}
.text-else {
  height: 600px;
  p {
    font-size: 18px;
    text-align: center;
    line-height: 600px;
    color: #333;
  }
}
.question-text {
  width: 100%;
  height: 237px;
  overflow: hidden;
  margin-bottom: 10px;
  position: relative;

  .text-top {
    .name {
      opacity: 1;
      font-size: 14px;
      font-family: PingFang SC;
      letter-spacing: 0px;
      margin: 24px 0 0 10px;
      position: relative;
      top: -5px;
    }
    img {
      width: 22px;
      height: 22px;
      margin: 16px 0 0 14px;
    }
    .show {
      overflow: hidden;
      .Qs {
        margin: 12px 0 0 45px;
        opacity: 1;
        font-size: 18px;
        font-family: Adobe Heiti Std;
        line-height: 24px;
        letter-spacing: 0px;
        display: block;
      }
    }
    .questionAvatar {
      font-size: 22px;
      display: inline-block;
      margin: 16px 0 0 14px;
      vertical-align: sub;
    }
  }
  .center {
    padding: 10px 10px 0 45px;
    .center-text {
      opacity: 1;
      font-size: 14px;
      font-family: Microsoft YaHei;
      line-height: 20px;
      letter-spacing: 0px;
      display: inline-block;
      margin: 0;

      p {
        margin: 0;
      }
    }
    .answerAudio {
      width: 215px;
      height: 40px;
      border-top-right-radius: 36px;
      border-bottom-right-radius: 36px;
      border-top-left-radius: 36px;
      padding: 2px 20px;
      margin-top: 30px;
    }
    .answerAudio-box {
      position: relative;
      width: 100%;
      height: 100%;
      .answerAudio-btn {
        display: inline-block;
        .iconfont {
          font-size: 25px;
          color: #f3f3f3;
          line-height: 40px;
          vertical-align: middle;
        }
      }
      .timer {
        color: #f3f3f3;
        vertical-align: middle;
        margin-left: 10px;
        font-size: 18px;
        display: inline-block;
      }
    }
  }
  .qrTime {
    position: absolute;
    bottom: 8px;
    left: 45px;
    .time {
      font-size: 14px;
      font-family: PingFang SC;
      letter-spacing: 0px;
    }
  }
  .text-bottom {
    overflow: hidden;
    position: absolute;
    bottom: 0;
    right: 0;
    ul {
      margin: 0;
      padding: 0;
      line-height: 36px;
      overflow: hidden;
      li {
        list-style: none;
        float: left;
        margin-right: 20px;
        a {
          text-decoration: none;
          .iconfont {
            font-size: 16px;
            margin-right: 3px;
            vertical-align: middle;
          }
          .show {
            color: #584945;
          }
          span:nth-child(2) {
            font-size: 16px;
            vertical-align: middle;
          }
        }
      }
    }
  }
}
.classics-text {
  overflow: hidden;
  .bookCtion {
    width: 189px;
    margin: 14px 27px 0 29px;
    float: left;
    height: 300px;
    position: relative;
    // &:nth-child(4n) {
    //   margin-right: 0;
    // }
    .bookImg {
      width: 100%;
      height: 180px;
      img {
        width: 100%;
        height: 100%;
        opacity: 1;
      }
    }
    .classic-title {
      text-align: center;
      margin-top: 4px;
    }
  }
  a {
    text-decoration: none;
    .bookBt {
      width: 45px;
      height: 45px;
      border: 1px solid;
      font-size: 12px;
      text-align: center;
      padding: 5px;
      border-radius: 50%;
      position: absolute;
      bottom: 10px;
      left: 70px;
    }
    .bookBt:hover {
      color: #fff;
      background: #584945;
    }
  }
}
.collection .el-tabs__header {
  top: -70px;
  left: 58px;
  width: 600px;
  margin: 0 0 -40px;
}
#myVideo {
  margin: 12px 40px 0 40px;
}
</style>

